body {
  margin: 0;
  padding:0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.MainPage {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.Header {
    background-color: #3264FF;
    min-height: 50px;
    height: 8vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.MainLogo {
    background-image: url(/Front-End-React-Test/static/media/NewOrder.3a58eb55.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    min-height: 42px;
    height: 6vh;
    min-width: 60px;
    width: 10vw;
}

.SearchUserForm {
    background-image: url(/Front-End-React-Test/static/media/logo.6ce24c58.svg);
    background-repeat: no-repeat;
    background-position: left center;
    background-size: contain;
    padding-left: 45px;
    padding-bottom: 2px;
    min-height: 30px;
    height: 4vh;
    width: 25vw;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    outline: none;
}

.PageContent {
    display: flex;
    flex-grow: 1;
    min-height: 420px;
}
.AccountPage {
    display: flex;
    flex: 1 1;
    background-color: #F9F9F9;
    flex-direction: row;
}

.AccountProfileInfo {
    display: flex;
    flex-direction: column;
    width: 20%;
    padding: 5vh 5vw 0 5vw;
}

.AccountAvatar {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    width: 20vw;
    height: 20vw;
    background-size: 20vw 20vw;
    border-radius: 50%;
    align-self: center;
}

.AccountName {
    text-align: left;
    padding: 5vh 0 2vh 0;
    font-size: x-large;
    font-weight: bold;
    overflow-wrap: anywhere;
    overflow: hidden;
}

.AccountNickname {
    text-align: left;
    padding: 0 0 2vh 0;
    font-size: x-large;
    color: blue;
    text-decoration: none;
    overflow-wrap: anywhere;
    overflow: hidden;
}

.RepositoriesAndPagination {
    display: flex;
    flex: 1 1;
    flex-direction: column;
}

.RepositoriesLabel {
    font-size: 32px;
    min-height: 50px;
    height: 12vh;
    line-height: max(12vh, 50px);
    vertical-align: middle;
}

.Repositories {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex: 1 1;
}

.UserNoRepositories {
    background-color: white;
    display: flex;
    flex: 1 1;
    background-image: url("https://i.kym-cdn.com/photos/images/original/001/548/016/cc1.gif");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
}

.AwaitingRepositories {
    display: flex;
    flex: 1 1;
    background-image: url("https://c.tenor.com/I6kN-6X7nhAAAAAj/loading-buffering.gif");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100px;
}


.RepositoryElement {
    display: flex;
    height: 25%;
}

.RepositoryElementContent {
    background-color: white;
    height: 80%;
    width: 90%;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 32px;
}

.RepositoryName {
    height: 40%;
    text-align: left;
    font-size: x-large;
    color: blue;
    text-decoration: none;
    overflow-wrap: anywhere;
    overflow: hidden;
}

.RepositoryDescription {
    height: 30%;
    overflow-wrap: anywhere;
    overflow: hidden;
}
.Pagination {
    display: flex;
    align-self: flex-end;
    flex-direction: row;
    justify-content: space-between;
    width: 25%;
    min-width: 300px;
    height: 10%;
    margin-right: 10%;
}

.DefaultButton {
    _background-color: transparent;
    cursor: pointer;
    border: 0;
    width: 30px;
    height: 30px;
    font-size: 16px;
    border-radius: 5px;
}

.PageButton {
}

.CurrentPageButton {
    background-color: #1964FF;
    color: white;
}

.ArrowButton {
    color: blue;
    font-weight: bold;
    font-size: 18px;
}

.ArrowButtonDisabled {
    background-color: transparent;
    opacity: 0.4;
    cursor: default;
}

.DotsButton {
    background-color: transparent;
    cursor: default;
}
.InitialPage {
    display: flex;
    flex: 1 1;
    background-color: #DDDDDD;
    background-image: url("https://i.pinimg.com/originals/70/f1/92/70f192cdc95e11bcf08297bbe54c6e5c.jpg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: auto 100%;
}
.NotFoundPage {
    display: flex;
    flex: 1 1;
    background-color: black;
    background-image: url("https://steamuserimages-a.akamaihd.net/ugc/925960612643321840/D18CD4FC8A345B20B731528AA347C2F1A80DC1AD/");
    background-repeat: repeat-y;
    background-position: top center;
    background-size: 100% auto;
}
